
html, body {
  background: #000;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Raleway", sans-serif !important;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #000;
}
::-webkit-scrollbar-thumb {
  background: #fff;
}
::-webkit-scrollbar-thumb:hover {
  background: #aaa;
}
a {
  color: #fff;
  text-decoration: none;
}
.footer-link:hover {
  text-decoration: underline;
}
a:visited {
  color: #fff;
}
.app-container {
  height: 100%;
  width: 100%;
}
.header {
  height: 100px;
  width: 100%;
  color: #fff;
  background: #000;
}
.header-inner {
  max-width: 800px;
  margin: 0 auto;
}
.header-link {
  margin-left: 1em;
}
.header-title {
  height: 100px;
  line-height: 100px;
  display: inline-block;
  font-size: 2em;
  font-variant: small-caps;
  font-weight: bold;
}
.header-title-sm {
  height: 100px;
  line-height: 100px;
  display: inline-block;
  font-size: 1.3em;
  font-variant: small-caps;
  font-weight: bold;
}
.thumb {
  margin-left: 20px;
  position: relative;
  top: 13px;
  margin-right: 20px;
  display: inline-block;
}
.header-links {
  margin-right: 1em;
  display: inline-block;
  float: right;
  line-height: 100px;
}
.content {
  width: 100%;
  min-height: calc(100% - 300px);
  background: #000;
}
.app-footer {
  height: 200px;
  width: 100%;
  color: #fff;
  background: #000;
}
.app-footer-inner {
  max-width: 800px;
  margin: 0 auto;
}
.app-footer-thumb {
  position: relative;
  top: 10px;
  left: 50px;
}
.app-footer-left {
  width: 220px;
  padding-left: 1em;
  display: inline-block;
}
.app-footer-left-bottom {
  position: relative;
  top: 2em;
  text-align: center;
  line-height: 2em;
}
.app-footer-left-bottom-top {
  font-size: 2em;
  margin-bottom: 0.3em;
  font-variant: small-caps;
}
.app-footer-right {
  padding-right: 2em;
  float: right;
}
.app-footer-right-top {
  margin-top: 1em;
}
.app-footer-right-top, .app-footer-right-second {
  margin-top: 1em;
  font-size: 0.9em;
  font-weight: bold;
  text-align: center;
}
.app-footer-right-middle {
  margin-top: 4em;
  margin-bottom: 4em;
}
.social-icon {
  margin-left: 0.5em;
  margin-right: 0.5em;
}
.footer-link {
  margin-left: 0.2em;
  margin-right: 0.2em;
}
.app-footer-right-bottom {
  font-size: 0.9em;
  text-align: center;
}
.icon-image {
  -webkit-filter: invert(1);
  filter: invert(1);
}



